/* Footer.css */
.footer {
  background-color: #333;
  color: white;
  padding: 10px;
  /* Reducir padding */
  text-align: center;
}

.footer-content {
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
  /* Menor espacio debajo del contenido */
}

.footer-content div {
  margin: 0;

  /* Eliminar márgenes laterales innecesarios */
  /* Asegura que el contenedor principal tome toda la altura */
  .page-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    /* Asegura que el contenedor ocupe la altura completa */
  }

  /* Contenido principal ocupa el espacio restante */
  .content {
    flex: 1;
    /* Expande el contenido principal para ocupar el espacio disponible */
  }

  /* Estilo del footer */
  .footer {
    background-color: #004080;
    /* Fondo del footer */
    color: #fff;
    text-align: center;
    padding: 10px 20px;
  }

  .footer-content h4 {
    margin-bottom: 5px;
    /* Reducir espacio entre título y contenido */
    font-size: 1.2rem;
    /* Ajustar tamaño del título */
  }

  .footer-bottom {
    font-size: 0.8rem;
    /* Hacer más pequeño el texto */
    margin-top: 5px;
    /* Reducir margen superior */
  }

  /* Media Queries para dispositivos pequeños */
  @media (max-width: 1030px) {
    .footer-content h4 {
      font-size: 1rem;
    }

    .footer-bottom {
      font-size: 0.7rem;
    }
  }
}