/* 🔹 Reset básico */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* 🔹 Contenedor principal con imagen de fondo */
.homepage-container {
  width: 100%;
  background-image: url('../assents/FOTOS ALDILA/0002.jpg');
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 20px; /* Añadido para evitar que el contenido toque los bordes */
}

/* 🔹 Capa de superposición para mejorar legibilidad del contenido */
.homepage-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1;
} 

/* 🔹 Contenedor del contenido central */
.homepage-content-wrapper {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 16px;
  padding: 30px;
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.2);
  max-width: 90%;
  width: 100%;
  flex-wrap: wrap; /* Permite que el contenido se reorganice en móviles */
  gap: 20px; /* Espacio entre elementos */
}

/* 🔹 Contenido de la izquierda */
.homepage-content {
  max-width: 50%;
  text-align: left;
  color: #2b2b2b;
}

.homepage-content h1 {
  font-size: 35px;
  font-weight: bold;
  color: #2b6cb0;
  margin-bottom: 10px;
}

.subtitle {
  font-size: 18px;
  color: #2b2b2b;
}

.subtitle span {
  font-weight: bold;
}

.description {
  margin: 20px 0;
  font-size: 16px;
  line-height: 1.5;
  color: #444;
}

/* 🔹 Botón de registro */
.register-button {
  background-color: #2b6cb0;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 30px;
  font-size: 18px;
  cursor: pointer;
  transition: transform 0.2s, background-color 0.3s;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  display: inline-block;
}

.register-button:hover {
  background-color: #1a4e7b;
  transform: scale(1.05);
}

/* 🔹 Mensaje de inicio de sesión */
.login-prompt {
  margin-top: 15px;
  font-size: 14px;
  text-align: center;
  padding: 5%;
  color: #555;
}

.login-link {
  color: #2b6cb0;
  font-weight: bold;
  text-decoration: none;
}

.login-link:hover {
  text-decoration: underline;
}

/* 🔹 Contenedor del logo */
.homepage-logo-container {
  flex-shrink: 0;
  text-align: center;
  margin-left: 50px; /* Espaciado a la izquierda en pantallas grandes */
}

.homepage-logo {
  width: 100%;
  height: auto;
  margin: 0 auto;
  border-radius: 8px;
  max-height: 350px; /* Ajuste para evitar que el logo sea demasiado grande */
}

/* 🔹 RESPONSIVE DESIGN */

/* 🔸 Tablets y pantallas medianas */
@media (max-width: 1030px) {
  .homepage-content-wrapper {
    flex-direction: column; /* Cambia el diseño a vertical */
    text-align: center;
    padding: 30%;
  }

  .homepage-logo-container {
    margin-left: 0; /* Elimina margen para centrarlo */
  }

  .homepage-logo {
    max-width: 80%;
    max-height: 300px;
  }

  .homepage-content {
    max-width: 100%;
  }
}

/* 🔸 Dispositivos móviles */
@media (max-width: 1030px) {
  .homepage-container {
    height: auto; /* ✅ Ajusta la altura según el contenido */
    min-height: 100vh; /* ✅ Asegura que cubra la pantalla */
  }

  .homepage-content-wrapper {
    flex-direction: column;
    text-align: center;
    padding: 20px;
  }

  .homepage-content h1 {
    font-size: 28px;
  }

  .subtitle {
    font-size: 16px;
  }

  .description {
    font-size: 14px;
  }

  .register-button {
    width: 100%;
    padding: 14px;
  }

  .homepage-logo-container {
    margin-top: 20px;
  }

  .homepage-logo {
    max-width: 100%;
    max-height: 250px;
  }
}
