.form-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-image: url('../assents/FOTOS ALDILA/0002.jpg');
    background-size: cover;
    background-position: center;
    position: relative;
  }

  .form-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4); /* Semitransparente para un efecto oscuro */
    backdrop-filter: blur(4.5px); /* Desenfoque aplicado al fondo */
    z-index: 1;
  }


  form {
    background: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    width: 100%;
    z-index: 2; /* Asegura que el formulario esté sobre el fondo */
    position: relative;
  }

  h2 {
    color: #fff; /* Ajusta el color del texto para mayor contraste */
    margin-bottom: 20px;
    z-index: 2; /* Asegura que el texto esté sobre el desenfoque */
    position: relative; /* Evita que se vea afectado por el fondo */
  }
  
  form label {
    display: block;
    margin-bottom: 10px;
    font-weight: bold;
  }
  
  form input {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  form button {
    width: 100%;
    padding: 10px;
    background-color: #0056b3;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  form button:hover {
    background-color: #0056b3;
  }
/* Estilos responsivos */
@media (max-width: 768px) {
    h2 {
      font-size: 1.5rem; /* Reduce el tamaño del texto en pantallas medianas */
    }
  
    form {
      max-width: 100%; /* Asegura que el formulario use todo el ancho disponible */
      padding: 15px; /* Reduce el padding en pantallas más pequeñas */
    }
  
    form input {
      padding: 8px; /* Reduce el padding de los campos de entrada */
    }
  
    form button {
      padding: 8px; /* Reduce el tamaño del botón */
    }
  }
  
  @media (max-width: 576px) {
    h2 {
      font-size: 1.2rem; /* Reduce aún más el tamaño del texto */
    }
  
    form {
      padding: 10px; /* Aumenta la compactación en pantallas pequeñas */
    }
  
    form input {
      padding: 6px; /* Ajusta el tamaño de los campos de entrada */
    }
  
    form button {
      padding: 6px; /* Ajusta el tamaño del botón */
    }
  }