/* Hero Header */
.hero-header {
  height: 50vh;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero-header h1 {
  color: #fff;
  font-size: 3rem;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7);
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0.4);
  padding: 20px 40px;
  border-radius: 10px;
}

/* Servicios */
.hero-services {
  padding: 50px 20px;
  text-align: center;
}

.hero-services h2 {
  font-size: 2rem;
  margin-bottom: 30px;
  text-transform: uppercase;
  color: #004080;
}

.services-grid {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  place-items: center;
}

.service-card {
  background: #f9f9f9;
  width: 200px;
  border-radius: 10px;
  padding: 30px 20px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.service-card i {
  font-size: 2.5rem;
  margin-bottom: 15px;
  color: #004080;
}

.service-card h3 {
  font-size: 1.2rem;
  color: #333;
}

.service-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
}

/* Galería */
.galeria-section {
  padding: 50px 20px;
  text-align: center;
}

.galeria-section h2 {
  font-size: 2rem;
  margin-bottom: 30px;
  text-transform: uppercase;
  color: #004080;
}

.galeria-grid {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  place-items: center;
}

.galeria-grid img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  transition: transform 0.3s ease;
}

.galeria-grid img:hover {
  transform: scale(1.05);
}

/* Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.modal-content {
  background: #fff;
  padding: 30px 20px;
  border-radius: 10px;
  position: relative;
  max-width: 400px;
  width: 90%;
  text-align: center;
}

.modal-content h2 {
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.8rem;
  color: #333;
}

.modal-content p {
  color: #555;
}

.modal-close {
  display: inline-block;
  margin-top: 20px;
  padding: 10px 20px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.modal-close:hover {
  background: #0056b3;
}

/* Responsiveness */
@media (max-width: 1030px) {
  .service-card {
    width: 150px;
    padding: 15px;
  }

  .service-card i {
    font-size: 2rem;
  }

  .service-card h3 {
    font-size: 1rem;
  }

  .modal-content {
    font-size: 0.9rem;
  }
}