.register-wrapper {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.register-container {
  
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
  color: #333;
  background-color: #f4f4f4;
}

.register-container h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
}

form {
  width: 100%;
  max-width: 300px;
  background-color: #ffffff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
}

/* Animación de los campos */
.form-group {
  opacity: 0;
  transform: translateX(-100%); /* Inicialmente fuera de la pantalla, a la izquierda */
  transition: all 0.5s ease;
  position: absolute;
  width: 100%;
}


.form-group.visible {
  opacity: 1;
  transform: translateX(0); /* Mueve al centro cuando es visible */
  position: relative;
}
label {
  display: block;
  margin-bottom: 15px;
  color: #333;
  font-weight: bold;
}

.input {
  width: 95%;
  padding: 10px;
  margin-top: 5px;
  border-radius: 5px;
  border: 1px solid #ddd;
  font-size: 1rem;
}

button {
  width: 100%;
  padding: 12px;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background-color: #28a745;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  transition: all 0.3s ease;
}

button:hover {
  background-color: #218838;
  transform: scale(1.05);
}

/* Carrusel */
.carousel-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  background-color: #000;
}

.carousel-register {
  display: flex;
  animation: slide 40s infinite linear;
  height: 100%;
  width: calc(100% * 6);
}

.carousel-register img {
  width: calc(100% / 1); /* Aumentar el tamaño para hacerlo más ancho */
  height: 100%;
  height: auto;
  object-fit: cover;
  flex-shrink: 0;
   margin-right: 10px; /* Espacio entre las imágenes */ 
}


.register-instructions {
  margin-top: 10px;
  font-size: 1rem;
  color: #555; /* Color gris */
  text-align: center;
}

.login-redirect {
  margin-top: 10px;
  font-size: 1rem;
  color: #333;
  text-align: center;
}

.login-redirect a {
  color: #007bff; /* Azul para el enlace */
  text-decoration: none;
}

.login-redirect a:hover {
  text-decoration: underline;
}



/* Animación del carrusel */
@keyframes slide {
  0% {
    transform: translateX(0);
  }
  16.6% {
    transform: translateX(-100%);
  }
  33.3% {
    transform: translateX(-200%);
  }
  50% {
    transform: translateX(-300%);
  }
  66.6% {
    transform: translateX(-400%);
  }
  83.3% {
    transform: translateX(-500%);
  }
  100% {
    transform: translateX(0);
  }
}


/* Medium devices (tablets, 600px to 768px) */
@media(max-width: 1030px) {
    .register-wrapper {
        flex-direction: column;
    }
    .form-register {
      max-width: 500px; /* ✅ Aumenta el tamaño SOLO en móviles */
      padding: 35px;
    }
}
