/* Estilo base */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20%;
  text-align: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Estilo para pantallas grandes (computadoras) */
@media (min-width: 1024px) {
  .App-header {
    font-size: calc(12px + 1.5vmin); /* Ajusta el tamaño de fuente para pantallas grandes */
  }

  .App-logo {
    height: 30vmin; /* Reduce el tamaño del logo */
  }
}

/* Estilo para pantallas medianas (tabletas y dispositivos pequeños) */
@media (min-width: 768px) and (max-width: 1023px) {
  .App-header {
    font-size: calc(10px + 2vmin); /* Tamaño intermedio de fuente */
  }

  .App-logo {
    height: 35vmin; /* Tamaño moderado del logo */
  }
}

/* Estilo para pantallas pequeñas (celulares) */
@media (max-width: 1030px) {
  .App-header {
    font-size: calc(8px + 2vmin); /* Reduce el tamaño de fuente */
    padding: 1rem; /* Agrega un relleno para evitar bordes ajustados */
  }

  .App-logo {
    height: 50vmin; /* Aumenta el tamaño del logo para pantallas pequeñas */
  }
}
