/* Reset basico */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  background-color: #333;
  color: white;
  position: relative;
  z-index: 1000;
}

.menu-toggle {
  font-size: 24px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  display: none;/* Ocultar en escritorio */
}

/* Estilo del menú principal */
.nav-list {
  display: flex;
  list-style: none;
}

.nav-item {
  position: relative;
}

.nav-link {
  padding: 10px 15px;
  color: white;
  text-decoration: none;
  display: block;
}

.nav-link:hover {
  background-color: #444;
}

/* Estilo del menu desplegable */
.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #444;
  list-style: none;
  width: 180px;
  z-index: 1000;
}

.dropdown-item a {
  display: block;
  padding: 10px;
  color: white;
  text-decoration: none;
}

.dropdown-item a:hover {
  background-color: #555;
}
/* ✅ Mostrar submenú en ESCRITORIO cuando se pasa el mouse */
.nav-item:hover .dropdown-menu {
  display: block;
}

/* ✅ RESPONSIVE DESIGN */
@media (max-width: 1030px){
  .menu-toggle{
    display: block; /* Mostrar botón hamburguesa */
    
  }
  /*Mostrar menu desplegable cuando esta activo */

.dropdown-menu.active {
  display: block;
}

  .nav-list {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    height: 100vh;  /* ✅ Asegura que ocupe toda la pantalla */
    background-color: #333;
  }

  .nav-list.active {
    display: flex;
  }

  .nav-item {
    text-align: center;
    width: 100%;
  }

  .dropdown-menu {
    position: absolute;/* Para que aparezca debajo de "Nuestras ofertas" */
    left: 50%;
    transform: translate(-50%);
    width: 50%;
    text-align: center;
    display: none; 
  }
  .nav-item.active .dropdown-menu {
    display: block;
  }
}


