.buttons-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 5px 5px; /* Reducir padding para hacer el marco más pequeño */
    background-color: #e7eaed;  
    border-radius: 8px; /* Reducir bordes redondeados */
    margin-top: -10px; /* Mover más arriba */
    max-width: 615px; /* Limitar el ancho */
    margin-left: auto;
    margin-right: auto; /* Centrar el contenedor */
}

.top-button {
    padding: 5px 12px; /* Reducir tamaño del botón */
    font-size: 18px; /* Reducir o aumentar tamaño de fuente */
    color: black;
    background-color: #e7eaed;  
    cursor: pointer;
    transition: all 0.3s ease;
}

.top-button:hover {
    background-color: #007bff;
    color: white;
}

.popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    padding: 70px; /* Incrementar el tamaño */
    border-radius: 20px; /* Mantener bordes redondeados */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    text-align: center;
    font-size: 24px; /* Aumentar tamaño del texto */
}


.close-button {
    margin-top: 20px; /* Espacio adicional para botón */
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    font-size: 18px; /* Texto más grande */
}

.close-button:hover {
    background-color: #0056b3;
}

.popup-content {
    max-width: 500px; /* Ajustar ancho máximo */
}

.main-content.blur-background {
    filter: blur(5px); /* Aplicar desenfoque solo al contenido principal */
    transition: filter 0.3s ease-in-out;
}

/* General Media Queries for Responsiveness */

/* Small devices (phones, 600px and down) */
@media (max-width: 600px) {
    body {
        font-size: 14px;
    }
}

/* Medium devices (tablets, 600px to 768px) */
@media (min-width: 601px) and (max-width: 768px) {
    body {
        font-size: 16px;
    }
}

/* Large devices (desktops, 768px and up) */
@media (min-width: 769px) {
    body {
        font-size: 18px;
    }
}
